
.userinfo {
  margin-top: 1.2rem;
  padding: 0 0.2rem;
}
.userinfo > .weui-cells {
  margin-top: 0;
}
.userinfo > .weui-cells:before,
.userinfo > .weui-cells:after,
.userinfo > .weui-cells .weui-cell:before {
  display: none;
}
.userinfo > .weui-cells .weui-cell {
  border-bottom: 0.01rem solid #ff0036;
}
.userinfo .vux-cell-bd {
  min-width: 2.27rem;
  white-space: nowrap;
}
.userinfo .vux-label {
  font-size: 0.34rem;
  color: #999;
}
.userinfo .weui-cell__ft {
  font-size: 0.34rem;
  color: #333;
}
.userinfo .tips {
  margin: 0.3rem 0.2rem;
  font-size: 0.24rem;
  color: #ff0000;
}
.userinfo .group-handle {
  margin-top: 1.23rem;
}
.userinfo .group-handle .untie {
  background-color: #ff0036;
}
.vux-alert .weui-dialog .weui-dialog__bd {
  color: #353535;
}



