
.group {
  margin-top: 1.2rem;
  padding: 0 0.2rem;
}
.group > .weui-cells {
  margin-top: 0;
}
.group > .weui-cells:before,
.group > .weui-cells .weui-cell:before {
  display: none;
}
.group > .weui-cells .weui-cell {
  border-bottom: 0.01rem solid #ff0036;
}
.group > .weui-cells .vux-x-input {
  padding: 0.53rem 0 0.33rem;
}
.group > .weui-cells .weui-label {
  color: #555;
}
.group .sendCodeBtn,
.group .countDownBtn {
  padding: 0;
  margin-left: 0.2rem;
  border: 0 none;
  line-height: 0.64rem;
}
.group .sendCodeBtn {
  font-size: 0.28rem;
  color: #ff8aa3;
}
.group .countDownBtn {
  margin-right: 0.7rem;
  font-size: 0.36rem;
  color: #ff0036;
}
.group .submit {
  margin-top: 1.1rem;
  background-color: #ff8aa3;
}
.group .submit:active {
  color: rgba(255, 255, 255, 0.6);
  background-color: #ce6e82!important;
}
.group .submit.action {
  background-color: #ff0036;
}



